import "./App.css";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import Home from "./components/home"
import Album from "./components/Album"
import Carousel from "./components/Carousel"
import Carousel2 from "./components/Carousel2"
import Cube from "./components/Cube"
import Gwiazdki from "./components/Gwiazdki"
import Spinner from "./components/Spinner.js"
import Ball from "./components/Ball.js"
import ResSetter from "./components/ResSetter"

function App() {
	return (
		<Router>
			<Routes>
				<Route path="/home" element={<Album/>}/>
				<Route path="/demo" element={<Album/>}/>
				<Route path="/demo1/:res" element={<ResSetter><Carousel></Carousel></ResSetter>}/>
				<Route path="/demo2/:res" element={<ResSetter><Carousel2></Carousel2></ResSetter>}/>
				<Route path="/demo3/:res" element={<ResSetter><Cube></Cube></ResSetter>}/>
				<Route path="/demo4/:res" element={<ResSetter><Gwiazdki></Gwiazdki></ResSetter>}/>
				<Route path="/demo5/:res" element={<ResSetter><Spinner></Spinner></ResSetter>}/>
				<Route path="/demo6/:res" element={<ResSetter><Ball></Ball></ResSetter>}/>
			</Routes>
		</Router>
	);
}

export default App;
