import React from "react";
import { useParams } from "react-router-dom";

function ResSetter(props) {
	let { res } = useParams();
	const getWidth = (res) => {
		switch (res) {
			case "1920x1080":
				return 1920;
			case "1366x768":
				return 1366;
			case "640x360":
				return 640;
			default:
				return "100%";
		}
	};
	let width = getWidth(res)-2;

	const getHeight = (res) => {
		switch (res) {
			case "1920x1080":
				return 1080;
			case "1366x768":
				return 768;
			case "640x360":
				return 360;
			default:
				return "100%";
		}
	};
	let height = getHeight(res)-2;
	console.log(`width: ${width} height: ${height}`);

	return (
		<div
			style={{ width: width, height: height, border: "1px solid red", overflowX:'clip', overflowY:'clip' }}
		>
            {props.children}
        </div>
	);
}

export default ResSetter;
