import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cube";
import "swiper/css/pagination";
import cube4 from "../media/images/cube4.jpg";
import cube3 from "../media/images/cube3.jpg";
import cube2 from "../media/images/cube2.jpg";
import cube1 from "../media/images/cube1.jpg";


import styles from "./stylesCube.module.css";

// import Swiper core and required modules
import SwiperCore, { EffectCube, Pagination } from "swiper";

// install Swiper modules
SwiperCore.use([EffectCube, Pagination]);

function Cube() {
	return (
		<Swiper
			effect={"cube"}
			grabCursor={true}
			cubeEffect={{
				shadow: true,
				slideShadows: true,
				shadowOffset: 20,
				shadowScale: 0.94,
			}}
			pagination={false}
			className={styles.swiper}
		>
			<SwiperSlide className={styles.swiperSlide}>
				<img src={cube1} alt='cianka kostki' />
			</SwiperSlide>
			<SwiperSlide className={styles.swiperSlide}>
				<img src={cube2} alt='cianka kostki' />
			</SwiperSlide>
			<SwiperSlide className={styles.swiperSlide}>
				<img src={cube3} alt='cianka kostki' />
			</SwiperSlide>
			<SwiperSlide className={styles.swiperSlide}>
				<img src={cube4} alt='cianka kostki' />
			</SwiperSlide>
		</Swiper>
	);
}

export default Cube;
